
body{margin:0;background-color:#e1e1e1
}
img{vertical-align:initial
}
.body-box .navbar{margin-bottom:0;z-index:9;width:100%;position:relative;background:#444;font-size:13px
}
.body-box .navbar .navbar-content{border:none;-webkit-box-shadow:none;box-shadow:none;width:90%;margin:auto;padding:5px
}
.body-box .navbar .navbar-content .navbar-logo{width:120px;display:inline-block;padding:3px 10px;height:34px;line-height:34px
}
.body-box .navbar .navbar-content .navbar-logo img{width:100%
}
@media only screen and (max-width: 520px){
.body-box .navbar .navbar-content .navbar-noright-link{float:none !important
}
}
.body-box .navbar .navbar-content .navbar-link{width:calc(100% - 180px);min-width:300px;text-align:right;height:38px;line-height:38px;float:right;padding-right:20px
}
.body-box .navbar .navbar-content .navbar-link span{margin:0 4px
}
.body-box .navbar .navbar-content .navbar-link span .el-link{color:#ccc
}
.body-box .navbar .navbar-content .navbar-link span .el-link:hover{color:#fff
}
.body-box .navbar .navbar-content .navbar-link .badge-item .el-badge__content{margin-top:10px;border:0px
}
.body-box .body-content{width:100%
}
@media only screen and (max-width: 950px){
.body-box .body-footer{visibility:hidden !important
}
}
.body-box .body-footer{clear:both;width:100%;height:82px;background-color:#fff;color:#ababab
}
.body-box .body-footer .footer-content{width:90%;margin:0px auto;padding:20px 0px;text-align:center
}
